.navbar {
	@apply z-[998] py-2 px-4 w-full fixed hover:opacity-100 transition-all ease-in-out duration-500;

	.container {
		@apply w-fit mx-auto h-full flex items-center justify-center;

		.logoMenuWrapper {
			@apply bg-white flex items-center justify-center gap-6 hover:bg-accent-default transition-all duration-500 ease-in-out;

			.logo {
				@apply object-contain object-center w-[35px] h-[35px] hover:opacity-50 transition-all duration-500 ease-in-out;
			}

			.localeTime {
				@apply hidden xl:flex font-primary text-tertiary-default text-center text-base tracking-[-0.02rem];
			}

			.menuBtn {
				@apply p-4;

				.menuText {
					@apply text-lg font-primary text-black tracking-wide;
				}
			}
		}

		.buttonSection {
			@apply hidden xl:flex items-center justify-end gap-6;

			.buttonStyling {
				@apply py-2 px-4 w-fit bg-accent-default rounded-full font-primary cursor-pointer text-pureBlack text-paragraph tracking-[0.05rem] hover:text-white hover:bg-pureBlack transition-all ease-in-out duration-500;
			}
		}
	}
}
